import {IconCategory} from '@here/hds-iconlibrary';
import * as React from 'react';
import {Flag, FlagVariant, FlagSize} from '@here/hds-flag';

// eslint-disable-next-line no-new
new Flag();

interface HDSFlagProperties extends React.HTMLAttributes<HTMLElement> {
  variant: FlagVariant;
  size: FlagSize;
  icon: string;
  iconCategory?: IconCategory;
}

const HDSFlag = React.forwardRef((props: HDSFlagProperties, ref) => {
  const {children, iconCategory, className, ...rest} = props;
  return (
    <hds-flag
      ref={ref}
      {...(iconCategory && {'icon-category': iconCategory})}
      {...(className && {class: className})}
      {...rest}
    >
      {children}
    </hds-flag>
  );
});

HDSFlag.defaultProps = {
  variant: 'subtle',
  size: 'small',
  icon: undefined,
  iconCategory: 'core-ui',
};

export default HDSFlag;
